.icon {
  height: 24px;
  width: 24px;
  display: block;
  background-size: cover;

  @include hover(transform, scale(1.2));

  @include onTablet {
    height: 32px;
    width: 32px;
  }

  &--phone {
    background-image: url(../images/icons/icon-phone.png);

    &:hover + .phone__bar--hover {
      opacity: 1;
      cursor: pointer;
    }

    &:focus + .phone__bar--hover {
      opacity: 1;
      cursor: pointer;
    }
  }

  &--menu {
    background-image: url(../images/icons/burger-menu.png);

    &:hover {
      background-image: url(../images/icons/burger-menu-hover.png);
    }
  }

  &--close {
    background-image: url(../images/icons/icon-close.png);
  }
}
